import React, { useEffect, useState } from "react";
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let Box = styled.div`
  padding : 20px;
`;

let Title = styled.h4`
  font-size : 25px;
  color: ${props => props.색상}
`;

function Detail(props) {

  let [alert, alertChange] = useState(true);
  let [inputData, inputDataChange] = useState('');

  // Detail 컴포넌트 로드 시 ajax로 데이터를 가져오고 싶음 -> useEffect
  useEffect(() => {
    // axios.get()


    let Timer = setTimeout(() => {
      alertChange(false)
    }, 2000);
    console.log('안녕');
    return (() => {clearTimeout(Timer)})
  }, []);


  useEffect(() => {

  });

    let { id } = useParams();
    let 찾은상품 = props.shoes.find(function(상품){
        return 상품.id == id
    });
    let history = useHistory();

  return (
    <div className="container">
      <Box>
        <Title className="red">상세페이지</Title>
      </Box>

      <input onChange={(e) => { inputDataChange(e.target.value) }} />
      
      {
        alert === true
          ? <div className="my-alert2">
              <p>재고가 얼마 남지 않았습니다</p>
            </div>
          : null
      }

      <div className="row">
        <div className="col-md-6">
          <img
            src="https://codingapple1.github.io/shop/shoes1.jpg"
            width="100%"
          />
        </div>
        <div className="col-md-6 mt-4">
            <h4 className="pt-5">{찾은상품.title}</h4>
            <p>{찾은상품.content}</p>
          <p>{찾은상품.price}원</p>
          
          <Info 재고 = {props.재고}></Info>

          {/* 주문하기 버튼을 누르면 재고 state에서 1 빼기 */}
          <button className="btn btn-danger" onClick={()=>{props.재고변경([9, 11, 12])}}>주문하기</button>
          <button className="btn btn-danger" onClick={() => {
              history.goBack();
          }}>뒤로가기</button>
        </div>
      </div>
    </div>
  );
}

function Info(props) {
  return(
    <p>재고: { props.재고[0] }</p>
  )
}

export default Detail;